import React, {Component} from "react"
import {render} from 'react-dom'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from '../actions/index'
import '../../css/tipsBox.less'
export class TipsBox extends Component {
  constructor(props) {
    super(props)
  }

  componentWillUpdate() {
    console.log("Login=>componentWillUpdate", this.props)
  }

  render() {
    return (
      <div className="tipsBox" style={{ "display": this.props.tips.messgage ? "block" : "none" } }>
        <div className="tipsBg"></div>
        <div className="main">
          <span className="text">{this.props.tips.messgage}</span></div>
      </div>
    )

  }
}

const mapStateToProps = (state) => {
  return {tips: state.tips}
}
const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(actions, dispatch),
  }
}
export default connect(
  mapStateToProps, // Redux store mapStateToProps  props , Redux store ownProps props propsmapStateToProps 
  mapDispatchToProps // Redux action creator Redux store  props  dispatch  dispatch  action creator  Redux  bindActionCreators() mapDispatchToProps dispatch  props  ownProps props propsmapDispatchToProps 
)(TipsBox)



