
import React,{Component} from "react"
import { render } from 'react-dom'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import * as actions from '../actions/index'
import '../../css/loginIngTop.less'

class LoginIngTop extends Component{
  render(){
    return <div className="loginIn-top">{this.props.nav.info.name} 
              <div className="btn" onClick={() => this.props.actions.loginSubmit("loginOut") }></div>
            </div>
  }
}
const mapStateToProps=(state)=>{
    return {nav:state.nav}
}
const mapDispatchToProps=(dispatch)=>{
    return {
        actions: bindActionCreators(actions, dispatch),
    }
}
export default connect(
    mapStateToProps,// Redux store mapStateToProps  props , Redux store ownProps props propsmapStateToProps 
    mapDispatchToProps// Redux action creator Redux store  props  dispatch  dispatch  action creator  Redux  bindActionCreators() mapDispatchToProps dispatch  props  ownProps props propsmapDispatchToProps 
    )(LoginIngTop)




