import React, {Component} from "react"
import {render} from 'react-dom'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from '../actions/index'
import Login from '../components/login'
import Reg from '../components/reg'
import '../../css/mobBox.less'
export class MobBox extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    let type = this.props.mobBoxData.isShow
    type = type ? (type == "loginShow" ? <Login /> : <Reg />) : type
    return (
      type && <div className="mobBox">
        <div className="bg" onClick={()=>{this.props.actions.mobBoxData("mobBoxClose")}}></div>
        {type}
      </div>
    )
  }
}

const mapStateToProps = (state)=> {
  return {mobBoxData: state.mobBoxData}
}
const mapDispatchToProps = (dispatch)=> {
  return {
    actions: bindActionCreators(actions, dispatch),
  }
}
export default connect(
  mapStateToProps,// Redux store mapStateToProps  props , Redux store ownProps props propsmapStateToProps 
  mapDispatchToProps// Redux action creator Redux store  props  dispatch  dispatch  action creator  Redux  bindActionCreators() mapDispatchToProps dispatch  props  ownProps props propsmapDispatchToProps 
)(MobBox)




