import React, {Component} from "react"
import {render} from 'react-dom'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from '../actions/index'
import '../../css/login.less'
export class Login extends Component {
    constructor(props) {
        super(props)
        this.userName
        this.password
    }

    componentWillUpdate() {
        console.log("Login=>componentWillUpdate", this.props)
        // this.props.actions.loginTop('loginIn')
    }

    submitLogin() {
        let _alert = this.props.actions._alert
        if (this.userName.value.length < 2) {
            _alert("")
            return
        }
        if (this.password.value.length < 6) {
            _alert("")
            return
        }
        let data = "userName=" + this.userName.value + "&password=" + this.password.value;
        this.props.actions.loginSubmit("loginSubmit", data)
    }

    render() {
        return (
            <form className="inner-mobBox login" onClick={(e)=>{e.stopPropagation()}}>
                <i className="close" onClick={()=>{this.props.actions.mobBoxData("mobBoxClose")}}>x</i>
                <div className="title">
                    <div className="top"></div>
                    <div className="bottom"></div>
                </div>
                <div className="user-info">
                    <div className="item">
                        <input type="text" name="userName" ref={el=>{this.userName=el}} maxLength="10" placeholder=""/>
                    </div>
                    <div className="item">
                        <input type="password" name="password" ref={el=>{this.password=el}} maxLength="16" placeholder=""/>
                    </div>
                </div>
                <div className="btn" onClick={()=>{this.submitLogin()}}></div>
            </form>
        )
    }
}

const mapStateToProps = (state)=> {
    return {login: state.mobBoxData}
}
const mapDispatchToProps = (dispatch)=> {
    return {
        actions: bindActionCreators(actions, dispatch),
    }
}
export default connect(
    mapStateToProps,// Redux store mapStateToProps  props , Redux store ownProps props propsmapStateToProps 
    mapDispatchToProps// Redux action creator Redux store  props  dispatch  dispatch  action creator  Redux  bindActionCreators() mapDispatchToProps dispatch  props  ownProps props propsmapDispatchToProps 
)(Login)




