import React, {Component} from "react"
import {render} from 'react-dom'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from '../actions/index'
import {Link, IndexLink} from 'react-router'
import NoLoginTop from '../components/noLoginTop'
import LoginIngTop from '../components/loginIngTop'
import {hashHistory} from 'react-router'
import '../../css/nav.less'

class Nav extends Component {
  constructor(props) {
    super(props)
    // this.keyword
  }

  componentWillMount() {
    this.props.actions.getUserInfo()
    console.log("Nav==>componentWillMount")
  }

  componentDidUpdate() {
    console.log("Nav==>componentDidUpdate")
  }

  handleSubmit() {
    if (!this.keyword.value) {
      this.props.actions._alert("");
      return
    }
    let keyword = location.hash.match(/keyword=[^&]+/) ? location.hash.match(/keyword=[^&]+/)[0].match(/[^=]+$/g)[0] : ''
    if (keyword == this.keyword.value) {
      return
    }

    const path = "/search?keyword=" + this.keyword.value
    hashHistory.push(path)
    this.props.actions.ajaxData('search', {'keyword': this.keyword.value})
  }

  render() {
    let props = this.props, isLogin
    if (props.nav.isLogin) {
      isLogin = <LoginIngTop />
    } else {
      isLogin = <NoLoginTop />
    }
    //
    return (
      <div className="head">
        <div className="content">
          <a href="/" className="logo">  </a>
          <div className="search-form">
            <input type="text" ref={el => { this.keyword = el }} name="keyword" placeholder="" />
            <div className="btn" onClick={() => { this.handleSubmit() }}> </div>
          </div>
          {isLogin}
        </div>
      </div>
    )
  }
}
const mapStateToProps = (state) => {
  return {nav: state.nav}
}
const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(actions, dispatch),
  }
}
export default connect(
  mapStateToProps, // Redux store mapStateToProps  props , Redux store ownProps props propsmapStateToProps 
  mapDispatchToProps // Redux action creator Redux store  props  dispatch  dispatch  action creator  Redux  bindActionCreators() mapDispatchToProps dispatch  props  ownProps props propsmapDispatchToProps 
)(Nav)



